import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom'
import './App.css';
import HomePage from './pages/homepage/homepage.component.jsx'
import ShopPage from './pages/shop/shop.component.jsx'
import Header from './components/header/header-component.jsx'
import SignInAndSignUpPage from './pages/sign-in-and-sign-up/sign-in-and-sign-up.component.jsx'
import { auth, createUserProfileDocument } from './firebase/firebase.utils'
import { connect } from 'react-redux'
import { setCurrentUser } from './redux/user/user.actions'
import { HashRouter } from "react-router-dom";
class App extends React.Component {

  //firebase
  unsubscribeFromAuth = null
  //firebase listening to authentication state changes
  componentDidMount() {
    const { setCurrentUser } = this.props;
    this.unsubscribeFromAuth = auth.onAuthStateChanged(async userAuth => {
      if (userAuth) {
        const userRef = await createUserProfileDocument(userAuth);
        //firebase getting the snapshot of the user id and its data as it wouldnt show the id otherwize
        userRef.onSnapshot(snapShot => {
          setCurrentUser({
            id: snapShot.id,
            ...snapShot.data()
          });
        });
      };

      setCurrentUser(userAuth)
    });
  }

  componentWillUnmount() {
    this.unsubscribeFromAuth();
  }

  render() {
    return (
      <HashRouter basename='https://federicodapote.github.io/crwn-clothing/'>
        <div>
          <Header />
          <Switch>
            <Route exact path='/crwn-clothing' component={HomePage} />
            <Route path='/shop' component={ShopPage} />
            {/* Redirects to previous page when logged in, and prevents going to log in page if already logged in */}
            <Route exact path='/signin'
              render={() =>
                this.props.currentUser ? (
                  <Redirect to='/' />
                ) : (
                    <SignInAndSignUpPage />
                  )
              }
            />
          </Switch>
        </div>
      </HashRouter>
    );
  }
}
const mapStateToProps = ({ user }) => ({
  currentUser: user.currentUser
})

const mapDispatchToProps = dispatch => ({
  setCurrentUser: user => dispatch(setCurrentUser(user))
})
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(App);
